.intro {
  min-height: 180px;
  background-color: transparent;
  background-image: url(../img/back-mobile.jpg);
  background-repeat: no-repeat;
  background-color: @desaturated-blue;
  color: @basic-white;
  background-size: 100%;
  padding: 70px 40px 47px;

  @media (min-resolution: 2dppx) {
      background: url(../img/back-mobile-form@2x.jpg);
      background-size: 100%;
    }


  @media (min-width: @tablet-width) {
  background-image: url(../img/back-tablet.jpg);
  background-repeat: no-repeat;
  padding-top: 36px;
  background-size: 100%;
  min-height: 480px;
    @media (min-resolution: 2dppx) {
      background: url(../img/back-tablet-form@2x.jpg);
      background-size: 100%;
    }
  }

  @media (min-width: @desktop-width) {
  background-image: url(../img/back-desktop.jpg);
  background-repeat: no-repeat;
  padding-top: 110px;
  background-size: 100%;
  min-height: 280px;

  @media (min-resolution: 2dppx) {
      background: url(../img/back-desktop-form@2x.jpg);
      background-size: 100%;
    }
  }

}

.intro__form {
  @media (min-width: @tablet-width) {
  min-height: auto;
  }

}

.intro__title {
  margin: 0 auto;
  font-size: 24px;
  line-height: 33px;
  font-weight: 300;
  text-align: center;

  @media (min-width: @tablet-width){
  font-size: 50px;
  line-height: 69px;
      padding: 58px 40px 47px;
  }
}
  @media (min-width: @desktop-width) {
  padding: 72px 0 77px;
  }
